							uni.showModal({
								title: '提示',
								content:  res.data.info,
								showCancel: false,
								confirmText: "关闭",
							});
						}
					},
					fail: () => {
						uni.hideLoading();
						uni.showToast({
							icon: 'none',
							title: '网络异常,请重试！'
						});
					},
				});
			}
