				method: 'GET',
				data: helper.postdata({
					"taskid": _self.rwid,
					"userid": helper.getstate().userid,
					"type": "2",
					"CState": cstate > 0 ? "0" : "1"
				}),
				success: res => {
					
					if (res.data.code == 200) {
						
						this.rwlist(this.rwid);
						uni.showToast({
							title: cstatetxt + "成功。"
						});

